import { useState, useRef, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import {
  ArrowLeft,
  MessageSquare,
  Clock,
  AlertCircle,
  Send,
  Calendar,
  CheckCircle,
  XCircle,
  CheckCheck,
} from "lucide-react";
import { toast } from "sonner";
import { usePermissions } from "@/context/PermissionContext";
import { useAuth } from "@/context/AuthContext";
import {
  getSupportTicketById,
  getTicketMessages,
  addTicketMessage,
  updateSupportTicket,
  type SupportTicketMessage,
} from "@/api/services/support/supportService";

export function SupportTicketDetail() {
  const { ticketId } = useParams<{ ticketId: string }>();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const [newMessage, setNewMessage] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const { isSuperAdmin } = usePermissions();
  const { user } = useAuth();
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Fetch ticket details
  const {
    data: ticketResponse,
    isLoading: ticketLoading,
    error: ticketError,
  } = useQuery({
    queryKey: ["supportTicket", ticketId],
    queryFn: () => getSupportTicketById(ticketId!),
    enabled: !!ticketId,
  });

  // Fetch ticket messages
  const {
    data: messagesResponse,
    isLoading: messagesLoading,
  } = useQuery({
    queryKey: ["supportTicketMessages", ticketId],
    queryFn: () => getTicketMessages(ticketId!),
    enabled: !!ticketId,
  });

  // Extract data from API responses
  const ticket = ticketResponse?.data;
  const messages = messagesResponse?.data;

  // Auto-scroll to bottom when messages change
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Add message mutation
  const addMessageMutation = useMutation({
    mutationFn: (messageData: { message: string }) =>
      addTicketMessage(ticketId!, messageData),
    onSuccess: () => {
      toast.success("Message sent successfully!");
      setNewMessage("");
      queryClient.invalidateQueries({ queryKey: ["supportTicketMessages", ticketId] });
      queryClient.invalidateQueries({ queryKey: ["supportTicket", ticketId] });
      queryClient.invalidateQueries({ queryKey: ["supportTickets"] });
      // Scroll to bottom after sending message
      setTimeout(scrollToBottom, 100);
    },
    onError: (error: any) => {
      toast.error(error.message || "Failed to send message");
    },
  });

  // Update ticket status mutation
  const updateStatusMutation = useMutation({
    mutationFn: (status: string) =>
      updateSupportTicket(ticketId!, { status }),
    onSuccess: () => {
      toast.success("Ticket status updated successfully!");
      queryClient.invalidateQueries({ queryKey: ["supportTicket", ticketId] });
      queryClient.invalidateQueries({ queryKey: ["supportTickets"] });
    },
    onError: (error: any) => {
      toast.error(error.message || "Failed to update ticket status");
    },
  });

  const handleSendMessage = async () => {
    if (!newMessage.trim()) return;

    setIsSubmitting(true);
    try {
      await addMessageMutation.mutateAsync({ message: newMessage.trim() });
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleStatusChange = (status: string) => {
    updateStatusMutation.mutate(status);
  };

  const getStatusBadgeVariant = (status: string) => {
    switch (status) {
      case "open":
        return "default";
      case "in_progress":
        return "secondary";
      case "waiting_response":
        return "outline";
      case "resolved":
        return "success";
      case "closed":
        return "destructive";
      default:
        return "default";
    }
  };

  const getPriorityBadgeVariant = (priority: string) => {
    switch (priority) {
      case "urgent":
        return "destructive";
      case "high":
        return "secondary";
      case "medium":
        return "outline";
      case "low":
        return "default";
      default:
        return "default";
    }
  };



  const formatDate = (dateString: string) => {
    try {
      console.log('üìÖ Formatting full date - Input:', dateString);

      // Handle different date formats that might come from backend
      let date: Date;

      if (typeof dateString === 'string') {
        // If it's a MySQL datetime string, ensure it's properly parsed
        // MySQL returns dates in 'YYYY-MM-DD HH:mm:ss' format in UTC
        if (dateString.includes(' ') && !dateString.includes('T')) {
          // Convert MySQL format to ISO format for proper timezone handling
          // Assume backend sends UTC time, so add 'Z' to indicate UTC
          const isoString = dateString.replace(' ', 'T') + 'Z';
          date = new Date(isoString);
          console.log('üìÖ MySQL format detected, converted to:', isoString, 'Parsed as:', date);
        } else if (dateString.includes('T') && !dateString.includes('Z') && !dateString.includes('+')) {
          // If it has 'T' but no timezone info, assume UTC
          const utcString = dateString + 'Z';
          date = new Date(utcString);
          console.log('üìÖ ISO without timezone, treated as UTC:', utcString, 'Parsed as:', date);
        } else {
          // Standard ISO format or other formats
          date = new Date(dateString);
          console.log('üìÖ Standard format:', dateString, 'Parsed as:', date);
        }
      } else {
        date = new Date(dateString);
      }

      // Check if date is valid
      if (isNaN(date.getTime())) {
        console.warn('üìÖ Invalid date string:', dateString);
        return 'Invalid date';
      }

      // Option 1: Convert to browser's local timezone
      const localFormatted = date.toLocaleString("en-US", {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
        hour12: true,
        timeZoneName: "short" // This will show the timezone (e.g., "PST", "EST")
      });

      // Option 2: Show in GMT/UTC with clear indicator
      const gmtFormatted = date.toLocaleString("en-US", {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
        hour12: true,
        timeZone: "UTC",
      }) + " GMT";

      console.log('üìÖ Local timezone format:', localFormatted);
      console.log('üìÖ GMT format:', gmtFormatted);
      console.log('üìÖ Browser timezone:', Intl.DateTimeFormat().resolvedOptions().timeZone);
      console.log('üìÖ Raw date object:', date);

      // Return local timezone format (you can change this to gmtFormatted if you prefer GMT)
      return localFormatted;

    } catch (error) {
      console.error('üìÖ Error formatting date:', error, 'Input:', dateString);
      return 'Invalid date';
    }
  };

  // Compact date format for message bubbles - ALWAYS show date and time
  const formatMessageTime = (dateString: string) => {
    try {
      console.log('üïê Formatting message time - Input:', dateString);
      console.log('üïê User browser timezone:', Intl.DateTimeFormat().resolvedOptions().timeZone);

      let date: Date;

      if (typeof dateString === 'string') {
        // Handle MySQL datetime format: "2024-12-30 15:30:00"
        if (dateString.includes(' ') && !dateString.includes('T')) {
          // MySQL format - DON'T assume UTC, treat as local server time
          // Let's try both approaches and see what works

          // Approach 1: Treat as UTC (add Z)
          const utcString = dateString.replace(' ', 'T') + 'Z';
          const utcDate = new Date(utcString);

          // Approach 2: Treat as local server time (no Z)
          const localString = dateString.replace(' ', 'T');
          const localDate = new Date(localString);

          console.log('üïê MySQL format - Original:', dateString);
          console.log('üïê As UTC (with Z):', utcString, '‚Üí', utcDate.toLocaleString());
          console.log('üïê As Local (no Z):', localString, '‚Üí', localDate.toLocaleString());

          // Use the UTC approach for now, but let's see both in console
          date = utcDate;

        } else if (dateString.includes('T') && !dateString.includes('Z') && !dateString.includes('+')) {
          // ISO format without timezone - assume UTC
          date = new Date(dateString + 'Z');
          console.log('üïê ISO without timezone, treated as UTC:', dateString + 'Z', 'Parsed as:', date);
        } else {
          // Standard format
          date = new Date(dateString);
          console.log('üïê Standard format:', dateString, 'Parsed as:', date);
        }
      } else {
        date = new Date(dateString);
      }

      if (isNaN(date.getTime())) {
        console.error('üïê Invalid date:', dateString);
        return 'Invalid time';
      }

      // Option 1: Show in user's local timezone
      const localFormatted = date.toLocaleString("en-US", {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
        hour12: true,
        timeZoneName: "short" // Shows PST, EST, etc.
      });

      // Option 2: Show in GMT/UTC with clear indicator
      const gmtFormatted = date.toLocaleString("en-US", {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
        hour12: true,
        timeZone: "UTC",
      }) + " GMT";

      console.log('üïê Local timezone format:', localFormatted);
      console.log('üïê GMT format:', gmtFormatted);
      console.log('üïê Raw date object:', date);
      console.log('üïê Date.getTime():', date.getTime());
      console.log('üïê Date.toISOString():', date.toISOString());

      // Return local timezone format (you can change this to gmtFormatted if you prefer GMT)
      return localFormatted;

    } catch (error) {
      console.error('üïê Error formatting message time:', error, 'Input:', dateString);
      return 'Invalid time';
    }
  };

  if (ticketLoading) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (ticketError || !ticket) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex flex-col items-center justify-center space-y-4">
        <AlertCircle className="h-12 w-12 text-gray-400" />
        <div className="text-center">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white">Ticket not found</h3>
          <p className="text-gray-500 dark:text-gray-400">
            The support ticket you're looking for doesn't exist or you don't have access to it.
          </p>
        </div>
        <Button onClick={() => navigate("/support")} variant="outline">
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Support
        </Button>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      {/* Simple Header */}
      <div className="bg-white dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700">
        <div className="px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <Button
                onClick={() => navigate("/support")}
                variant="outline"
                size="sm"
              >
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back
              </Button>
              <div>
                <h1 className="text-xl font-semibold text-gray-900 dark:text-white">
                  {ticket?.title || 'Loading...'}
                </h1>
                <p className="text-sm text-gray-500 dark:text-gray-400">
                  #{ticket?.ticket_id?.slice(-8).toUpperCase() || 'Loading...'}
                </p>
              </div>
            </div>

            {/* Status change buttons - Only for Super Admin */}
            {isSuperAdmin && (
              <div className="flex items-center space-x-2">
                {ticket?.status !== "resolved" && (
                  <Button
                    onClick={() => handleStatusChange("resolved")}
                    size="sm"
                    disabled={updateStatusMutation.isPending}
                  >
                    <CheckCircle className="h-4 w-4 mr-2" />
                    Mark Resolved
                  </Button>
                )}
                {ticket?.status !== "closed" && (
                  <Button
                    onClick={() => handleStatusChange("closed")}
                    variant="outline"
                    size="sm"
                    disabled={updateStatusMutation.isPending}
                  >
                    <XCircle className="h-4 w-4 mr-2" />
                    Close
                  </Button>
                )}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="px-4 sm:px-6 lg:px-8 py-6">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Left Column - Main Content */}
          <div className="lg:col-span-2 space-y-6">
            {/* Ticket Description */}
            <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
              <h2 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
                Description
              </h2>
              <p className="text-gray-700 dark:text-gray-300 whitespace-pre-wrap leading-relaxed">
                {ticket?.description || 'Loading...'}
              </p>
            </div>

            {/* Tags */}
            {ticket?.tags && ticket.tags.length > 0 && (
              <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
                <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
                  Tags
                </h3>
                <div className="flex flex-wrap gap-2">
                  {ticket.tags.map((tag: any, index: number) => (
                    <Badge key={index} variant="secondary">
                      {tag}
                    </Badge>
                  ))}
                </div>
              </div>
            )}

            {/* Messages Section */}
            <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg overflow-hidden flex flex-col h-[600px]">
              {/* Messages Header */}
              <div className="flex items-center justify-between p-6 border-b border-gray-200 dark:border-gray-700 flex-shrink-0">
                <h2 className="text-lg font-medium text-gray-900 dark:text-white">
                  Messages
                </h2>
                <span className="text-sm text-gray-500 dark:text-gray-400">
                  {messages?.length || 0} messages
                </span>
              </div>

              {/* Messages Container with Internal Scroll */}
              <div className="flex-1 overflow-y-auto p-6 space-y-4">
                {messagesLoading ? (
                  <div className="flex items-center justify-center py-8">
                    <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                  </div>
                ) : messages && messages.length > 0 ? (
                  <>
                    {messages.map((message: SupportTicketMessage) => {
                      const isCurrentUser = String(message.user_id) === String(user?.id);

                      return (
                        <div key={message.id} className={`flex ${isCurrentUser ? 'justify-end' : 'justify-start'}`}>
                          <div className={`flex items-start space-x-3 max-w-[70%] ${isCurrentUser ? 'flex-row-reverse space-x-reverse' : ''}`}>
                            {/* Avatar */}
                            <div className="flex-shrink-0">
                              <Avatar className="h-8 w-8 ring-2 ring-white dark:ring-gray-800 shadow-sm">
                                <AvatarFallback className={`text-white text-xs font-semibold ${
                                  isCurrentUser
                                    ? 'bg-gradient-to-br from-blue-500 to-blue-600'
                                    : 'bg-gradient-to-br from-gray-500 to-gray-600'
                                }`}>
                                  {message.first_name?.charAt(0) || 'U'}
                                  {message.last_name?.charAt(0) || 'N'}
                                </AvatarFallback>
                              </Avatar>
                            </div>

                            {/* Message Content */}
                            <div className={`flex flex-col ${isCurrentUser ? 'items-end' : 'items-start'}`}>
                              {/* Message Header */}
                              <div className={`flex items-center space-x-2 mb-1 ${isCurrentUser ? 'flex-row-reverse space-x-reverse' : ''}`}>
                                <span className="text-xs font-medium text-gray-600 dark:text-gray-400">
                                  {isCurrentUser ? 'You' : `${message.first_name} ${message.last_name}`}
                                </span>
                                <span className="text-xs text-gray-400 dark:text-gray-500">
                                  {formatMessageTime(message.created_at)}
                                </span>
                              </div>

                              {/* Message Bubble */}
                              <div className={`relative px-4 py-3 rounded-2xl shadow-sm ${
                                isCurrentUser
                                  ? 'bg-blue-500 text-white rounded-br-md'
                                  : 'bg-white dark:bg-gray-700 text-gray-800 dark:text-gray-200 border border-gray-200 dark:border-gray-600 rounded-bl-md'
                              }`}>
                                <p className="text-sm leading-relaxed whitespace-pre-wrap">
                                  {message.message}
                                </p>

                                {/* Message tail */}
                                <div className={`absolute top-0 w-3 h-3 ${
                                  isCurrentUser
                                    ? 'right-0 bg-blue-500 transform rotate-45 translate-x-1/2 -translate-y-1/2'
                                    : 'left-0 bg-white dark:bg-gray-700 border-l border-t border-gray-200 dark:border-gray-600 transform rotate-45 -translate-x-1/2 -translate-y-1/2'
                                }`}></div>
                              </div>

                              {/* Message Status */}
                              {isCurrentUser && (
                                <div className="flex items-center space-x-1 mt-1 text-xs text-gray-400">
                                  <CheckCheck className="h-3 w-3" />
                                  <span>Sent</span>
                                </div>
                              )}
                            </div>
                          </div>
                        </div>
                      );
                    })}
                    {/* Scroll anchor */}
                    <div ref={messagesEndRef} />
                  </>
                ) : (
                  <div className="text-center py-8">
                    <MessageSquare className="h-12 w-12 text-gray-300 mx-auto mb-4" />
                    <p className="text-gray-500 dark:text-gray-400">No messages yet</p>
                  </div>
                )}
              </div>

              {/* Message Input - Fixed at Bottom */}
              <div className="p-6 border-t border-gray-200 dark:border-gray-700 flex-shrink-0 bg-gray-50 dark:bg-gray-800/50">
                <div className="space-y-4">
                  <Textarea
                    placeholder="Type your message..."
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    rows={3}
                    disabled={isSubmitting}
                    className="resize-none bg-white dark:bg-gray-700"
                  />
                  <div className="flex justify-end">
                    <Button
                      onClick={handleSendMessage}
                      disabled={!newMessage.trim() || isSubmitting}
                      size="sm"
                      className="bg-blue-600 hover:bg-blue-700"
                    >
                      <Send className="h-4 w-4 mr-2" />
                      {isSubmitting ? "Sending..." : "Send"}
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Right Column - Ticket Information */}
          <div className="space-y-6">
            {/* Ticket Status */}
            <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
                Ticket Information
              </h3>
              <div className="space-y-4">
                <div className="flex justify-between items-center">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Status</span>
                  <Badge variant={getStatusBadgeVariant(ticket?.status || 'open')}>
                    {ticket?.status?.replace('_', ' ') || 'Loading...'}
                  </Badge>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Priority</span>
                  <Badge variant={getPriorityBadgeVariant(ticket?.priority || 'medium')}>
                    {ticket?.priority?.toUpperCase() || 'LOADING...'}
                  </Badge>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Category</span>
                  <Badge variant="outline">
                    {ticket?.category?.replace('_', ' ') || 'Loading...'}
                  </Badge>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Messages</span>
                  <span className="text-sm text-gray-900 dark:text-white">
                    {ticket?.message_count || 0}
                  </span>
                </div>
              </div>
            </div>

            {/* Dates */}
            <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
                Timeline
              </h3>
              <div className="space-y-3">
                <div className="flex items-center space-x-3">
                  <Calendar className="h-4 w-4 text-gray-400" />
                  <div>
                    <p className="text-xs text-gray-500 dark:text-gray-400">Created</p>
                    <p className="text-sm text-gray-900 dark:text-white">
                      {ticket?.created_at ? formatDate(ticket.created_at) : 'Loading...'}
                    </p>
                  </div>
                </div>
                <div className="flex items-center space-x-3">
                  <Clock className="h-4 w-4 text-gray-400" />
                  <div>
                    <p className="text-xs text-gray-500 dark:text-gray-400">Last Updated</p>
                    <p className="text-sm text-gray-900 dark:text-white">
                      {ticket?.updated_at ? formatDate(ticket.updated_at) : 'Loading...'}
                    </p>
                  </div>
                </div>
                {ticket?.resolved_at && (
                  <div className="flex items-center space-x-3">
                    <CheckCircle className="h-4 w-4 text-green-500" />
                    <div>
                      <p className="text-xs text-gray-500 dark:text-gray-400">Resolved</p>
                      <p className="text-sm text-gray-900 dark:text-white">
                        {formatDate(ticket.resolved_at)}
                      </p>
                    </div>
                  </div>
                )}
                {ticket?.closed_at && (
                  <div className="flex items-center space-x-3">
                    <XCircle className="h-4 w-4 text-red-500" />
                    <div>
                      <p className="text-xs text-gray-500 dark:text-gray-400">Closed</p>
                      <p className="text-sm text-gray-900 dark:text-white">
                        {formatDate(ticket.closed_at)}
                      </p>
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/* User Information */}
            <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
                Created By
              </h3>
              <div className="flex items-center space-x-3">
                <Avatar className="h-10 w-10">
                  <AvatarFallback className="bg-blue-100 text-blue-600">
                    {ticket?.user_first_name?.charAt(0) || 'U'}
                    {ticket?.user_last_name?.charAt(0) || 'N'}
                  </AvatarFallback>
                </Avatar>
                <div>
                  <p className="text-sm font-medium text-gray-900 dark:text-white">
                    {ticket?.user_first_name || 'Unknown'} {ticket?.user_last_name || 'User'}
                  </p>
                  <p className="text-xs text-gray-500 dark:text-gray-400">
                    {ticket?.user_email || 'No email provided'}
                  </p>
                </div>
              </div>
            </div>

            {/* Assigned To */}
            {ticket?.assigned_first_name && (
              <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
                <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
                  Assigned To
                </h3>
                <div className="flex items-center space-x-3">
                  <Avatar className="h-10 w-10">
                    <AvatarFallback className="bg-green-100 text-green-600">
                      {ticket?.assigned_first_name?.charAt(0) || 'A'}
                      {ticket?.assigned_last_name?.charAt(0) || 'S'}
                    </AvatarFallback>
                  </Avatar>
                  <div>
                    <p className="text-sm font-medium text-gray-900 dark:text-white">
                      {ticket?.assigned_first_name} {ticket?.assigned_last_name}
                    </p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">
                      {ticket?.assigned_email}
                    </p>
                  </div>
                </div>
              </div>
            )}

            {/* Additional Information */}
            <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
                Details
              </h3>
              <div className="space-y-2 text-sm">
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">Ticket ID</span>
                  <span className="text-gray-900 dark:text-white font-mono">
                    {ticket?.ticket_id || 'Loading...'}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">Database ID</span>
                  <span className="text-gray-900 dark:text-white">
                    #{ticket?.id || 'N/A'}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">User ID</span>
                  <span className="text-gray-900 dark:text-white">
                    {ticket?.user_id || 'N/A'}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
